import { TransactionsListOptions } from './transactions-list-options.model';
import { TransactionsExportOptions } from './transactions-export-options.model';
import { TransactionsFilterOptions } from './transactions-filter-options.model';

export interface TransactionsGetParams {
  amountGreaterThan?: number;
  amountLessThan?: number;
  bookingDateGreaterThan?: string;
  bookingDateLessThan?: string;
  description?: string;
  reference?: string;
  counterPartyName?: string;
  counterPartyAccountNumber?: string;
  creditDebitIndicator?: string;
  categories?: string[];
  billingStatus?: string;
  currency?: string;
  notes?: number;
  id?: string;
  locale?: string;
  arrangementsIds?: string[];
  fromCheckSerialNumber?: number;
  toCheckSerialNumber?: number;
  checkSerialNumbers?: string[];
  query?: string;
  from?: number;
  cursor?: string;
  size?: number;
  orderBy?: string;
  direction?: string;
  secDirection?: string;
  exportType?: string;
  state?: string;
  types?: string[];
  typeGroups?: string[];
}
/* eslint-disable no-redeclare*/
// eslint-disable-next-line @typescript-eslint/no-namespace
export namespace TransactionsGetParams {
  // eslint-disable-next-line complexity
  export const toHttpGetParams = (getParams: TransactionsListOptions | TransactionsExportOptions) => {
    const params: TransactionsGetParams = {
      ...TransactionsFilterOptions.toHttpGetParams(getParams.filter),
    };

    if (getParams.account && getParams.account.arrangementId) {
      params['arrangementsIds'] = Array.isArray(getParams.account.arrangementId)
        ? getParams.account.arrangementId
        : [getParams.account.arrangementId];
    }

    if (getParams.search && getParams.search.query) {
      params['query'] = getParams.search.query;
    }

    if ((getParams as TransactionsListOptions).pagination) {
      params['from'] = (getParams as TransactionsListOptions).pagination.from;
      params['size'] = (getParams as TransactionsListOptions).pagination.size;
    }

    if (getParams.sort) {
      params['orderBy'] = getParams.sort.orderBy.toString();
      params['direction'] = getParams.sort.direction.toString();
    }

    if ((getParams as TransactionsExportOptions).exportType) {
      params['exportType'] = (getParams as TransactionsExportOptions).exportType;
    }

    if ((getParams as TransactionsExportOptions).locale) {
      params['locale'] = (getParams as TransactionsExportOptions).locale;
    }

    if ((getParams as TransactionsListOptions).state) {
      params['state'] = (getParams as TransactionsListOptions).state;
    }

    return params;
  };
}
/* eslint-enable no-redeclare*/
